// components/SignupModal.js
import React, { useState } from 'react';
import axios from 'axios';

const SignupModal = ({ onClose }) => {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSignup = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/signup', { username, email, password });
      alert('Signup successful. Please log in.');
      onClose(); // Close the signup modal
    } catch (error) {
      console.error('Error during signup:', error);
      alert('Signup failed. Please try again.');
    }
  };

  return (
    <div className="modal fade show" tabIndex="-1" aria-labelledby="signup-modal-label" aria-hidden="true" style={{ display: 'block' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="signup-modal-label">Sign Up</h5>
            <button type="button" className="btn-close" aria-label="Close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <form id="signup-form" onSubmit={handleSignup}>
              <label htmlFor="signup-username">Username</label>
              <input type="text" id="signup-username" name="username" required className="form-control" value={username} onChange={(e) => setUsername(e.target.value)} />
              <label htmlFor="signup-email">Email</label>
              <input type="email" id="signup-email" name="email" required className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} />
              <label htmlFor="signup-password">Password</label>
              <input type="password" id="signup-password" name="password" required className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} />
              <button type="submit" id="signup-submit" className="btn btn-primary mt-2">Sign Up</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SignupModal;