// pages/index.js
import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import '../styles/home.css';
import Navbar from '../components/navbar';
import LoginModal from '../components/LoginModal';
import SignupModal from '../components/SignupModal';
import AddMatchModal from '../components/AddMatchModal';
import AddTeamModal from '../components/AddTeamModal';
import { useAuth } from '../context/AuthContext';

const Home = () => {
  const [matches, setMatches] = useState([]);
  const [teams, setTeams] = useState([]);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showSignupModal, setShowSignupModal] = useState(false);
  const [showAddMatchModal, setShowAddMatchModal] = useState(false);
  const { currentUser, logout, isLoginModalOpen, setIsLoginModalOpen } = useAuth();

  useEffect(() => {
    fetchMatches();
    fetchTeams();
  }, []);

  const fetchMatches = () => {
    fetch('/api/matches')
      .then(response => response.json())
      .then(data => setMatches(data))
      .catch(error => console.error('Error fetching matches:', error));
  };

  const fetchTeams = () => {
    fetch('/api/teams')
      .then(response => response.json())
      .then(data => setTeams(data))
      .catch(error => console.error('Error fetching teams:', error));
  };

  const deleteMatch = (id) => {
    fetch(`/api/matches/${id}`, {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => {
        if (response.ok) {
          fetchMatches();
        } else {
          console.error('Failed to delete match');
        }
      })
      .catch(error => console.error('Error during delete request:', error));
  };

  const sortTable = (column, order) => {
    const sortedMatches = [...matches].sort((a, b) => {
      let compareA = a[column];
      let compareB = b[column];

      if (column === 'match_date') {
        compareA = new Date(a[column]);
        compareB = new Date(b[column]);
      }

      if (compareA < compareB) {
        return order === 'asc' ? -1 : 1;
      } else if (compareA > compareB) {
        return order === 'asc' ? 1 : -1;
      } else {
        return 0;
      }
    });
    setMatches(sortedMatches);
  };
  return (
    <>
      <Head>
        <meta charSet="UTF-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, viewport-fit=cover" />
        
        <title>Football Matches</title>
      </Head>
      <div className="bg-light">
        <div className="main-content">
        <Navbar currentUser={currentUser} logout={logout} />
  
          <div id="signup-modal" className="modal fade" tabIndex="-1" aria-labelledby="signup-modal-label" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="signup-modal-label">Sign Up</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form id="signup-form">
                    <label htmlFor="signup-username">Username</label>
                    <input type="text" id="signup-username" name="username" required className="form-control" />
                    <label htmlFor="signup-email">Email</label>
                    <input type="email" id="signup-email" name="email" required className="form-control" />
                    <label htmlFor="signup-password">Password</label>
                    <input type="password" id="signup-password" name="password" required className="form-control" />
                    <button type="submit" id="signup-submit" className="btn btn-primary mt-2">Sign Up</button>
                  </form>
                </div>
              </div>
            </div>
          </div>
  
          <div id="login-modal" className="modal fade" tabIndex="-1" aria-labelledby="login-modal-label" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="login-modal-label">Log In</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form id="login-form">
                    <label htmlFor="login-username">Username</label>
                    <input type="text" id="login-username" name="username" required className="form-control" />
                    <label htmlFor="login-password">Password</label>
                    <input type="password" id="login-password" name="password" required className="form-control" />
                    <button type="submit" id="login-submit" className="btn btn-primary mt-2">Log In</button>
                  </form>
                  <div className="mt-3 text-center">
                    <a href="#" id="show-signup-link" className="text-decoration-none">Don't have an account? <strong>Sign up now!</strong></a>
                  </div>
                </div>
              </div>
            </div>
          </div>
  
          <div className="modal fade" id="addMatchModal" tabIndex="-1" aria-labelledby="addMatchModalLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="addMatchModalLabel">Add New Match</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form id="addMatchForm">
                    <div className="form-step" id="step1">
                      <div className="mb-3">
                        <label htmlFor="homeTeam" className="form-label">Home Team</label>
                        <select id="homeTeam" className="form-control">
                          <option value="">Select Home Team</option>
                          {teams.map(team => (
                            <option key={team.team_name} value={team.team_name}>{team.team_name}</option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="form-step" id="step2">
                      <div className="mb-3">
                        <label htmlFor="awayTeam" className="form-label">Away Team</label>
                        <select id="awayTeam" className="form-control">
                          <option value="">Select Away Team</option>
                          {teams.map(team => (
                            <option key={team.team_name} value={team.team_name}>{team.team_name}</option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="form-step" id="step3">
                      <div className="mb-3">
                        <label htmlFor="venue" className="form-label">Venue</label>
                        <input type="text" id="venue" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step4">
                      <div className="mb-3">
                        <label htmlFor="homeTeamLogo" className="form-label">Home Team Logo (PNG URL)</label>
                        <input type="text" id="homeTeamLogo" className="form-control" placeholder="Enter path to Home Team logo" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="awayTeamLogo" className="form-label">Away Team Logo (PNG URL)</label>
                        <input type="text" id="awayTeamLogo" className="form-control" placeholder="Enter path to Away Team logo" required />
                      </div>
                    </div>
                    <div className="form-step" id="step5">
                      <div className="mb-3">
                        <label htmlFor="matchDate" className="form-label">Match Date</label>
                        <input type="date" id="matchDate" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step6">
                      <div className="mb-3">
                        <label htmlFor="homeScore" className="form-label">Home Score</label>
                        <input type="number" id="homeScore" className="form-control" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="awayScore" className="form-label">Away Score</label>
                        <input type="number" id="awayScore" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step7">
                      <div className="mb-3">
                        <label htmlFor="possessionHome" className="form-label">Home Team Possession (%)</label>
                        <input type="number" id="possessionHome" className="form-control" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="possessionAway" className="form-label">Away Team Possession (%)</label>
                        <input type="number" id="possessionAway" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step8">
                      <div className="mb-3">
                        <label htmlFor="passesHome" className="form-label">Home Team Passes</label>
                        <input type="number" id="passesHome" className="form-control" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="passesAway" className="form-label">Away Team Passes</label>
                        <input type="number" id="passesAway" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step9">
                      <div className="mb-3">
                        <label htmlFor="foulsHome" className="form-label">Home Team Fouls</label>
                        <input type="number" id="foulsHome" className="form-control" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="foulsAway" className="form-label">Away Team Fouls</label>
                        <input type="number" id="foulsAway" className="form-control" required />
                      </div>
                    </div>
                    <div className="form-step" id="step10">
                      <div className="mb-3">
                        <label htmlFor="scorers" className="form-label">Scorers</label>
                        <div id="scorersContainer">
                          <div className="scorer-input">
                            <input type="text" className="form-control scorer-player" placeholder="Player Name" />
                            <input type="text" className="form-control scorer-team" placeholder="Team (Home/Away)" />
                            <input type="text" className="form-control scorer-goal-time" placeholder="Goal Time (e.g. 20')" />
                          </div>
                        </div>
                        <button type="button" id="addScorerBtn" className="btn btn-secondary">Add Scorer</button>
                      </div>
                    </div>
                    <div className="form-navigation text-center">
                      <button type="button" id="prevBtn" className="btn btn-secondary" style={{ display: "none" }}>Previous</button>
                      <button type="button" id="nextBtn" className="btn btn-primary">Next</button>
                      <button type="submit" className="btn btn-primary" style={{ display: "none" }} id="submitBtn">Submit</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
  
          <div className="modal fade" id="addTeamModal" tabIndex="-1" aria-labelledby="addTeamModalLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="addTeamModalLabel">Add New Team</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form id="addTeamForm">
                    <div className="mb-3">
                      <label htmlFor="teamName" className="form-label">Team Name</label>
                      <input type="text" id="teamName" className="form-control" placeholder="Enter Team Name" required />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="teamLogo" className="form-label">Team Logo (URL)</label>
                      <input type="text" id="teamLogo" className="form-control" placeholder="Enter URL of Team Logo" required />
                    </div>
                    <div className="text-center">
                      <button type="submit" id="submitTeamBtn" className="btn btn-success">Submit</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
  
          <div className="matches-container">
            <table id="matchesTable" className="table table-striped table-hover table-bordered rounded shadow">
              <thead className="table-dark sticky-top">
                <tr>
                  <th className="text-center">
                    Home Team
                    <span id="home_team_up" className="sorting-arrow" onClick={() => sortTable('home_team', 'asc')}>&#8593;</span>
                    <span id="home_team_down" className="sorting-arrow" onClick={() => sortTable('home_team', 'desc')}>&#8595;</span>
                  </th>
                  <th className="text-center">
                    Away Team
                    <span id="away_team_up" className="sorting-arrow" onClick={() => sortTable('away_team', 'asc')}>&#8593;</span>
                    <span id="away_team_down" className="sorting-arrow" onClick={() => sortTable('away_team', 'desc')}>&#8595;</span>
                  </th>
                  <th className="text-center">
                    Match Date
                    <span id="match_date_up" className="sorting-arrow" onClick={() => sortTable('match_date', 'asc')}>&#8593;</span>
                    <span id="match_date_down" className="sorting-arrow" onClick={() => sortTable('match_date', 'desc')}>&#8595;</span>
                  </th>
                  <th className="text-center">Home Score</th>
                  <th className="text-center">Away Score</th>
                  <th className="text-center">Action</th>
                </tr>
              </thead>
              <tbody>
                {matches.map(match => (
                  <tr key={match.id}>
                    <td><a href={`match.js?id=${match.id}`} className="table-link">{match.home_team}</a></td>
                    <td><a href={`match.js?id=${match.id}`} className="table-link">{match.away_team}</a></td>
                    <td>{new Date(match.match_date).toLocaleDateString()}</td>
                    <td>{match.home_score}</td>
                    <td>{match.away_score}</td>
                    <td><button onClick={() => deleteMatch(match.id)} className="btn btn-danger btn-sm">Delete</button></td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
  
          <footer>
            <div className="container">
              <p className="mb-0">&copy; 2024 Football Matches. All rights reserved.</p>
            </div>
          </footer>
        </div>
      </div>
    </>
  );
};

export default Home;