// components/AddMatchModal.js
import React, { useState } from 'react';

const AddMatchModal = ({ teams, onClose }) => {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({
    homeTeam: '',
    awayTeam: '',
    venue: '',
    homeTeamLogo: '',
    awayTeamLogo: '',
    matchDate: '',
    homeScore: '',
    awayScore: '',
    possessionHome: '',
    possessionAway: '',
    passesHome: '',
    passesAway: '',
    foulsHome: '',
    foulsAway: '',
    scorers: [{ player: '', team: '', goalTime: '' }]
  });

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [id]: value
    }));
  };

  const handleScorerChange = (index, field, value) => {
    const newScorers = [...formData.scorers];
    newScorers[index][field] = value;
    setFormData((prevData) => ({
      ...prevData,
      scorers: newScorers
    }));
  };

  const addScorer = () => {
    setFormData((prevData) => ({
      ...prevData,
      scorers: [...prevData.scorers, { player: '', team: '', goalTime: '' }]
    }));
  };

  const handlePrev = () => setStep((prevStep) => Math.max(prevStep - 1, 1));
  const handleNext = () => setStep((prevStep) => Math.min(prevStep + 1, 10));

  const handleSubmit = (e) => {
    e.preventDefault();
    // Add your form submission logic here
    console.log('Form submitted:', formData);
  };

  return (
    <div className="modal fade show" tabIndex="-1" aria-labelledby="addMatchModalLabel" aria-hidden="true" style={{ display: 'block' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="addMatchModalLabel">Add New Match</h5>
            <button type="button" className="btn-close" aria-label="Close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <form id="addMatchForm" onSubmit={handleSubmit}>
              {step === 1 && (
                <div className="form-step" id="step1">
                  <div className="mb-3">
                    <label htmlFor="homeTeam" className="form-label">Home Team</label>
                    <select id="homeTeam" className="form-control" value={formData.homeTeam} onChange={handleChange}>
                      <option value="">Select Home Team</option>
                      {teams.map((team) => (
                        <option key={team.team_name} value={team.team_name}>{team.team_name}</option>
                      ))}
                    </select>
                  </div>
                </div>
              )}
              {step === 2 && (
                <div className="form-step" id="step2">
                  <div className="mb-3">
                    <label htmlFor="awayTeam" className="form-label">Away Team</label>
                    <select id="awayTeam" className="form-control" value={formData.awayTeam} onChange={handleChange}>
                      <option value="">Select Away Team</option>
                      {teams.map((team) => (
                        <option key={team.team_name} value={team.team_name}>{team.team_name}</option>
                      ))}
                    </select>
                  </div>
                </div>
              )}
              {step === 3 && (
                <div className="form-step" id="step3">
                  <div className="mb-3">
                    <label htmlFor="venue" className="form-label">Venue</label>
                    <input type="text" id="venue" className="form-control" value={formData.venue} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 4 && (
                <div className="form-step" id="step4">
                  <div className="mb-3">
                    <label htmlFor="homeTeamLogo" className="form-label">Home Team Logo (PNG URL)</label>
                    <input type="text" id="homeTeamLogo" className="form-control" value={formData.homeTeamLogo} onChange={handleChange} required />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="awayTeamLogo" className="form-label">Away Team Logo (PNG URL)</label>
                    <input type="text" id="awayTeamLogo" className="form-control" value={formData.awayTeamLogo} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 5 && (
                <div className="form-step" id="step5">
                  <div className="mb-3">
                    <label htmlFor="matchDate" className="form-label">Match Date</label>
                    <input type="date" id="matchDate" className="form-control" value={formData.matchDate} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 6 && (
                <div className="form-step" id="step6">
                  <div className="mb-3">
                    <label htmlFor="homeScore" className="form-label">Home Score</label>
                    <input type="number" id="homeScore" className="form-control" value={formData.homeScore} onChange={handleChange} required />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="awayScore" className="form-label">Away Score</label>
                    <input type="number" id="awayScore" className="form-control" value={formData.awayScore} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 7 && (
                <div className="form-step" id="step7">
                  <div className="mb-3">
                    <label htmlFor="possessionHome" className="form-label">Home Team Possession (%)</label>
                    <input type="number" id="possessionHome" className="form-control" value={formData.possessionHome} onChange={handleChange} required />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="possessionAway" className="form-label">Away Team Possession (%)</label>
                    <input type="number" id="possessionAway" className="form-control" value={formData.possessionAway} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 8 && (
                <div className="form-step" id="step8">
                  <div className="mb-3">
                    <label htmlFor="passesHome" className="form-label">Home Team Passes</label>
                    <input type="number" id="passesHome" className="form-control" value={formData.passesHome} onChange={handleChange} required />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="passesAway" className="form-label">Away Team Passes</label>
                    <input type="number" id="passesAway" className="form-control" value={formData.passesAway} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 9 && (
                <div className="form-step" id="step9">
                  <div className="mb-3">
                    <label htmlFor="foulsHome" className="form-label">Home Team Fouls</label>
                    <input type="number" id="foulsHome" className="form-control" value={formData.foulsHome} onChange={handleChange} required />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="foulsAway" className="form-label">Away Team Fouls</label>
                    <input type="number" id="foulsAway" className="form-control" value={formData.foulsAway} onChange={handleChange} required />
                  </div>
                </div>
              )}
              {step === 10 && (
                <div className="form-step" id="step10">
                  <div className="mb-3">
                    <label htmlFor="scorers" className="form-label">Scorers</label>
                    <div id="scorersContainer">
                      {formData.scorers.map((scorer, index) => (
                        <div key={index} className="scorer-input">
                          <input
                            type="text"
                            className="form-control scorer-player"
                            placeholder="Player Name"
                            value={scorer.player}
                            onChange={(e) => handleScorerChange(index, 'player', e.target.value)}
                          />
                          <input
                            type="text"
                            className="form-control scorer-team"
                            placeholder="Team (Home/Away)"
                            value={scorer.team}
                            onChange={(e) => handleScorerChange(index, 'team', e.target.value)}
                          />
                          <input
                            type="text"
                            className="form-control scorer-goal-time"
                            placeholder="Goal Time (e.g. 20')"
                            value={scorer.goalTime}
                            onChange={(e) => handleScorerChange(index, 'goalTime', e.target.value)}
                          />
                        </div>
                      ))}
                    </div>
                    <button type="button" id="addScorerBtn" className="btn btn-secondary" onClick={addScorer}>Add Scorer</button>
                  </div>
                </div>
              )}
              <div className="form-navigation text-center">
                <button type="button" id="prevBtn" className="btn btn-secondary" onClick={handlePrev} style={{ display: step > 1 ? 'inline-block' : 'none' }}>Previous</button>
                <button type="button" id="nextBtn" className="btn btn-primary" onClick={handleNext} style={{ display: step < 10 ? 'inline-block' : 'none' }}>Next</button>
                <button type="submit" className="btn btn-primary" style={{ display: step === 10 ? 'inline-block' : 'none' }} id="submitBtn">Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddMatchModal;