// components/LoginModal.js
import React, { useState } from 'react';
import axios from 'axios';
import { useAuth } from '../context/AuthContext';

const LoginModal = ({ onClose, showSignupModal }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const { setCurrentUser } = useAuth();

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/login', { username, password });
      const { token } = response.data;
      localStorage.setItem('token', token);
      setCurrentUser(username);
      onClose();
      window.location.reload(); // Reload to refresh the authentication state
    } catch (error) {
      console.error('Error during login:', error);
      alert('Login failed. Please try again.');
    }
  };

  return (
    <div className="modal fade show" tabIndex="-1" aria-labelledby="login-modal-label" aria-hidden="true" style={{ display: 'block' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="login-modal-label">Log In</h5>
            <button type="button" className="btn-close" aria-label="Close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <form id="login-form" onSubmit={handleLogin}>
              <label htmlFor="login-username">Username</label>
              <input type="text" id="login-username" name="username" required className="form-control" value={username} onChange={(e) => setUsername(e.target.value)} />
              <label htmlFor="login-password">Password</label>
              <input type="password" id="login-password" name="password" required className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} />
              <button type="submit" id="login-submit" className="btn btn-primary mt-2">Log In</button>
            </form>
            <div className="mt-3 text-center">
              <a href="#" id="show-signup-link" className="text-decoration-none" onClick={showSignupModal}>Don't have an account? <strong>Sign up now!</strong></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginModal;